


































































































.index-content {
  position: absolute;
  top: 34px;
  bottom: 0;
  width: 100%;
}
.index-member {
  position: absolute;
  top: 10px;
  z-index: 999;
  width: 300px;
  bottom: 10px;
  margin-left: 10px;
  .search-box {
    position: relative;
    .search-button {
      position: absolute;
      right: 0;
      height: 100%;
      line-height: 1px;
    }
  }
  .index-member-list {
    position: absolute;
    top: 40px;
    bottom: 45px;
    width: 100%;
  }
  .index-member-box {
    position: absolute;
    top: 38px;
    bottom: 0;
    background: #fff;
    width: 100%;
  }
  .index-menber-title {
    text-align: center;
    height: 40px;
    line-height: 40px;
    font-size: 12px;
    color: #4b77be;
    border-bottom: 1px solid #ddd;
  }
  .el-menu-item {
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #ddd;
    &.active {
      background: #4b77be;
      color: #fff;
      [class^='el-icon'] {
        color: #fff;
      }
    }
    .title {
      vertical-align: middle;
      span {
        display: inline-block;
        width: 84px;
        overflow: hidden;
        vertical-align: middle;
      }
    }
  }
  .index-pagination-box {
    height: 45px;
    background: #fff;
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 10px 0;
    padding-left: 10px;
    border-top: 1px solid #ddd;
    .jump-button {
      padding: 0 5px;
      span {
        line-height: 26px;
      }
    }
    .jump-input {
      width: 40px;
      .el-input__inner {
        width: 38px;
      }
    }
    .pageTotal-box {
      margin: 0 5px;
    }
  }
  .index-pagination {
    /deep/ .el-pagination__jump {
      margin-left: 0;
    }
    /deep/ .el-pagination__sizes {
      margin: 0;
    }
    /deep/ .el-input__suffix {
      display: none;
    }
    /deep/ .el-select {
      height: 22px;
      .el-input {
        width: 60px;
        font-size: 12px;
        .el-input__inner {
          padding-right: 0;
          border-radius: 14px;
          padding-left: 0;
          text-align: center;
        }
      }
      .el-input--mini {
        .el-input__inner {
          height: 20px;
          line-height: 20px;
        }
      }
    }
    /deep/ .el-pagination__editor.el-input {
      width: 38px;
    }

    /deep/ .el-pagination__total {
      margin-right: 2px;
    }
  }
}
