

































.app-wrapper {
  position: relative;
  height: 100%;
  width: 100%;
}
.main-content {
  &:-webkit-full-screen {
    background-color: #fff !important;
  }
  &:-moz-full-screen {
    background-color: #fff !important;
  }
  &:-ms-fullscreen {
    background-color: #fff !important;
  }
  &:fullscreen {
    background-color: #fff !important;
  }
}
