// cover some element-ui styles

.el-breadcrumb__inner,
.el-breadcrumb__inner a {
  font-weight: 400 !important;
}

.el-upload {
  input[type="file"] {
    display: none !important;
  }
}

.el-upload__input {
  display: none;
}

.cell {
  .el-tag {
    margin-right: 0px;
  }
}

.small-padding {
  .cell {
    padding-left: 5px;
    padding-right: 5px;
  }
}

.fixed-width {
  .el-button--mini {
    padding: 7px 10px;
    min-width: 60px;
  }
}

.status-col {
  .cell {
    padding: 0 10px;
    text-align: center;

    .el-tag {
      margin-right: 0px;
    }
  }
}

// to fixed https://github.com/ElemeFE/element/issues/2461
.el-dialog {
  transform: none;
  left: 0;
  position: relative;
  margin: 0 auto;
}

// refine element ui upload
.upload-container {
  .el-upload {
    width: 100%;

    .el-upload-dragger {
      width: 100%;
      height: 200px;
    }
  }
}

// dropdown
.el-dropdown-menu {
  a {
    display: block
  }
}

// fix date-picker ui bug in filter-item
.el-range-editor.el-input__inner {
  display: inline-flex !important;
}

// to fix el-date-picker css style
.el-range-separator {
  box-sizing: content-box;
}

.pop-boxer {
  position: absolute;
  line-height: 1;
  background-color: white;
  filter: drop-shadow(0 1px 4px rgba(0, 0, 0, 0.2));
  padding: 5px;
  border-radius: 10px;
  border: 1px solid #cccccc;
  bottom: 12px;
  left: -50px;
  min-width: 280px;
  &:before, &:after {
    top: 100%;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
  }
  &:before {
    border-top-color: #cccccc;
    border-width: 11px;
    left: 57px;
    margin-left: -11px;
  }
  &:after {
    border-top-color: white;
    border-width: 10px;
    left: 57px;
    margin-left: -10px;
  }
}

.pop-container p {
  margin: 20px 0;
}

.pop-closer {
  text-decoration: none;
  position: absolute;
  top: 2px;
  right: 8px;
  color: #4b77be;
  &:after {
    content: "";
  }
  &:hover {
    color: #23527c;
  }
}

//  tab 
.snt-card-tabs {
  &.el-tabs--card {
    & > .el-tabs__header {
      .el-tabs__nav {
        border: none;
      }
      .el-tabs__item {
        border-radius: 5px 5px 0 0;
        border-left: 1px solid transparent;
        border-right: 1px solid transparent;
        border-top: 1px solid transparent;
        &.is-active {
          border-top-color: #dfe4ed;
          border-right-color: #dfe4ed;
          border-left-color: #dfe4ed;
        }
      }
    }
  }
  .el-tabs__item {
    height: 25px;
    line-height: 24px;
  }
}

